// Expose String, Objects, Methods on parent component

const alertChange = () => {
  let birthDate = new Date(date_of_birth.value);
  validate_date_of_birth.value = new Date(date_of_birth.value);
  let today = new Date();
  let age = today.getFullYear() - birthDate.getFullYear();
  var month = today.getMonth() - birthDate.getMonth();

  if (month < 0 || (month === 0 && today.getDate() < birthDate.getDate())) {
    age--;
  }

  if (age <= 4) {
    covidHidden = true;
    cv_received.value = "";
    cv_received_boolean = true;
    is_cv_received = true;
    cv_brand_name.value = "";
    cv_brand_name_boolean = true;
    cv_brand_name_boolean_1 = true;
    noVaccine = true;
    vaccineHasOne = true;
    firstDose.value = "";
    secondDose.value = "";
    validate_first_dose.value = "";
    validate_second_dose.value = "";
    hideBooster1 = true;
    hideBooster2 = true;
    cv_brand_name_boolean = true;
    cv_brand_name_boolean_1 = true;
  } else if (age > 4) {
    covidHidden = false;
    cv_received_boolean = false;
  } else {
    covidHidden = true;
  }
  date_boolean = covidHidden;
};

const alertPassportDate = () => {
